@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200&family=Roboto:ital,wght@1,900&display=swap');
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
p{
    margin-bottom: 0;
}
.header{
    width: 100%;
    height: 70px;
    background-color:#cac8c8 ;
    display: flex;
    align-items: center;
    justify-content: space-around;
}
.logo{
    font-family: 'Roboto', sans-serif;
    padding-left: 15px;
    font-size: 30px;
}

.header-link ul{
    display: flex;
    list-style: none;
        margin-bottom: 0;
    padding-bottom: 0;    
}
.header-link ul li{
    font-size: 17px;
    font-weight: 600;
    text-transform: uppercase;
    cursor: pointer;
}

.search-bar{
    background-color: antiquewhite;
    height: 40px;
    width: 300px;
    border-radius: 10px;
    display: flex;
    align-items: center;
    padding-left: 10px;
    padding-right: 5px;
}
.search-bar .search{
    margin-right: 7px;
}
.search-bar input{
    width: 250px;
    border: none;
    outline: none;
    background-color: transparent;

} 
.cart-header{
    display: flex;
    align-items: center;

}
.cart-header img{
    margin-right: 8px;
    cursor: pointer;
}
.cart-header p{
    font-size: 18px;
    font-weight: 600;
}
.dropbtn {
    display: inline-block;
    color: rgb(0, 0, 0);
    text-align: center;
    padding: 20px 16px;
    text-decoration: none;
    font-size: 17px;
    font-weight: 600;
}




li.dropdown {
    display: inline-block;
}

.dropdown-content {
    display: none;
    position: absolute;
    background-color:#fff;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
    z-index: 1;

}

.dropdown-content a {
    
    padding: 10px 16px;
    text-decoration: none;
    display: block;
    text-align: left;
    color: rgb(2, 2, 2);
    text-transform: capitalize;
}

.dropdown:hover .dropdown-content {
    display: block;
}